import React from 'react';
import Item from './Item';
import './List.scss';

const List = ({lists}) => {
    const renderedList = lists.map((list) => {

        return (
            <Item 
                task = {list.task}
                priority = {list.priority}
            />
        );
    });


    return <div>{renderedList}</div>
}

export default List;